<template>
    <div>
        <input placeholder="Enter Pokemon here" type="text" v-model="text" />
    </div>

    <div>
        <div v-for="pokemon in filteredPokemons" :key="pokemon.name">
            <router-link :to="`/about/${urlIdLookup[pokemon.name]}`">
                {{ pokemon.name }}
            </router-link>
        </div>
    </div>
</template>

<script>
import axios from "axios";

export default {
    name: "Home",
    data() {
        return {
            pokemons: [],
            text: "",
            urlIdLookup: {},
        };
    },
    computed: {
        filteredPokemons() {
            if (!this.text) {
                return [];
            }

            return this.pokemons.filter((pokemon) =>
                pokemon.name.includes(this.text)
            );
        },
    },
    created() {
        axios
            .get(`https://pokeapi.co/api/v2/pokemon/?offset=0&limit=2000`)
            .then((response) => {
                this.pokemons = response.data.results;
                this.urlIdLookup = response.data.results.reduce((acc, cur) => {
                    const urlParts = cur.url.split("/");
                    return {
                        ...acc,
                        [cur.name]: urlParts[urlParts.length - 2],
                    };
                }, {});
            });
    },
};
</script>